.logo a svg {
  width: 70px;
  height: 38px;
  fill: $color-alice-blue;

  @media (max-width: $vp-1023) {
    width: 38px;
    height: 20px;
  }

  @media (max-width: $vp-767) {
    width: 42px;
    height: 22px;
  }
}

.logo--js {
  position: relative;
  margin-left: 35px;
  z-index: 2;
}

.logo--js a svg {
  @media (max-width: $vp-767) {
    width: 42px;
    height: 22px;
    fill: $color-tangaroa;
  }
}
